.home_container {
    width: 100%;
    height: 100vh;
    background: no-repeat top center url("https://raw.githubusercontent.com/Yan-Cam/blogPictureHost/main/home_bg.jpg");
    background-size: cover;
    background-attachment: fixed;
}
.home_btn_container {
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    width: 250px;
    height: 200px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: column;
    bottom: 40px;
}